.wrapper {
  display: flex;
  flex-direction: column;
 
  min-height: 70vh;
  padding-top: 120px;
  max-width: 1280px;
  margin: auto;
}

.button-holder{
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.list {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  padding: 0px 25px;
}

.item {
  flex-basis: calc((100% - 30px) / 2);
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

@media screen and (min-width: 480px) {
  .item {
    flex-basis: calc((100% - 60px) / 3)
  }
}

@media screen and (min-width: 768px) {
  .item {
    flex-basis: calc((100% - 90px) / 4)
  }
}

@media screen and (min-width: 1200px) {
  .item {
    flex-basis: calc((100% - 150px) / 6)
  }
}

.image-holder {
  width: 100%;
  height: 100%;
  position: relative;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 15px;
  overflow: hidden;
}

.data {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 10px;
  position: absolute;
  background-color: #212020cd;
  width: 100%;
  bottom: 0;
  padding: 10px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  justify-content: center;
}

.title {
  font-size: 12px;
  font-weight: 400;
  text-align: center;
}